import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import CatalogHighlightComp from './CatalogHighlightComp';
import CatalogItem from './CatalogItem';
import _ from 'helpers/lodash';
import {event} from 'helpers/analitics';
import {ensureAbsolute} from 'helpers/utils';
import gtm from 'helpers/gtm';
import constants from 'const';
import './catalog.scss';

export class CatalogComponent extends React.Component {
  catalogDownloadEvent = (locale) => {
    event('CatalogDownload', `${this.props.externalNumber}_${locale}`);
    gtm.event(gtm.CATALOGUE_DOWNLOAD, {'form_language': locale});
  };

  render() {
    const {
      extras,
      lots,
      auction,
      headline,
      seeMore,
      download,
      downloadCh,
      catalogUrl,
      chineseCatalogUrl
    } = this.props;

    const isShopEvent = auction.auctionType === constants.AUCTION_SHOP_EVENT;
    const tripletLots = lots.sort((a, b) => a.catalogNumber - b.catalogNumber).reduce((triplets, lot, index)=> {
      const tripleIndex = Math.round((index - 1) / 3);
      triplets[tripleIndex] = triplets[tripleIndex] || [];
      triplets[tripleIndex].push(lot);
      return triplets;
    }, []);
    const tripletElements = tripletLots.map((triplet, index) => {
      return [<div key={index} className="clearfix">{triplet.map((lot, innerIndex) => {
        return (<CatalogItem {...this.props} lot={lot} key={innerIndex} isShopEvent={isShopEvent} />);
      })}</div>,
        <CatalogHighlightComp lots={triplet} extras={extras} seeMoreText={seeMore} isShopEvent={isShopEvent} />];
    });
    const downloadLink = makeDownloadLink(catalogUrl, download, _.partial(this.catalogDownloadEvent, this.props.locale));
    const downloadChineeseLink = makeDownloadLink(chineseCatalogUrl, downloadCh, _.partial(this.catalogDownloadEvent, 'ch'));

    return <div className="section-wrapper">
      <div className="catalog-component__container">
        <h3 id="auction-landing-page__catalog" className="px1">
          {headline}
          {!isShopEvent && <span className="catalog-component__download">
                    {downloadLink}
                    {downloadChineeseLink}
                  </span>}
        </h3>
        {tripletElements}
      </div>
    </div>;
  }
}

function makeDownloadLink(catalogUrl, download, evt) {
  return catalogUrl ? (<a target="_blank" className="link link--with-icon link--download"
                          href={ensureAbsolute(catalogUrl)}
                          onClick={evt}>{download}</a>) : '';
}

CatalogComponent.propTypes = {
  lots: PropTypes.array,
  extras: PropTypes.object,
  headline: PropTypes.string,
  seeMore: PropTypes.string,
  download: PropTypes.string,
  downloadCh: PropTypes.string,
  catalogUrl: PropTypes.string,
  chineseCatalogUrl: PropTypes.string,
  externalNumber: PropTypes.number,
  locale: PropTypes.string,
  auction: PropTypes.object,
  userLoggedIn: PropTypes.bool,
  invalidLotText: PropTypes.string
};

CatalogComponent.defaultProps = {
  auction: {},
  items: [],
  url: '',
  lotImgWidth: 300,
  lotImgHeight: 225,
  mobileLotImgWidth: 300,
  mobileLotImgHeight: 225,
  absenteeBidUrl: '',
  absenteeBidUrlForLoggedInUser: '',
  userLoggedIn: false
};

function mapStateToProps(state) {
  return {
    locale: state.modules.module.locale,
    externalNumber: _.get(state, 'modules.data.auction.externalNumber'),
    userLoggedIn: !!state.user.email,
    auction: _.get(state, 'modules.data.auction'),
    enableAbsenteeBidding: _.get(state, 'config.enableAbsenteeBidding')
  };
}

export default connect(mapStateToProps)(CatalogComponent);



