import React from 'react';
import EmailButton from 'components/action-button-email/EmailButton';

const ActionButtons = (props) => {

  return (
    <div className="shop-lot-teaser__action-button">
      <EmailButton
        body={props.questionEmailBody}
        to={props.questionEmailRecipient}
        subject={props.questionEmailSubject}
        title={props.questionEmailButtonTitle}
        dynamicTitle={props.dynamicTitle}
        userEmail={props.user.email}
        icon="link--question"
      />
      <EmailButton
        body={props.recommendEmailBody}
        subject={props.recommendEmailSubject}
        title={props.recommendEmailButtonTitle}
        dynamicTitle={props.dynamicTitle}
        userEmail={props.user.email}
      />
    </div>
  );
};

ActionButtons.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  user: React.PropTypes.object,
  recommendEmailBody: React.PropTypes.string,
  recommendEmailSubject: React.PropTypes.string,
  recommendEmailButtonTitle: React.PropTypes.string,
  questionEmailBody: React.PropTypes.string,
  questionEmailSubject: React.PropTypes.string,
  questionEmailRecipient: React.PropTypes.string,
  questionEmailButtonTitle: React.PropTypes.string,
  dynamicTitle: React.PropTypes.string,
};

export default ActionButtons;



