import React from 'react';
import LinkElement from 'components/link-element/LinkElement';
import gtm from 'helpers/gtm';
import {isBrowser, auctionDateToString} from 'helpers/utils';


const EmailButton = (props) => {
  const subject = encodeURIComponent(replaceVariables(props.subject, props));
  const to = props.to;
  const body = encodeURIComponent(replaceVariables(props.body, props));

  const link = `mailto:${to}?subject=${subject}&body=${body}`;
  const click = clickHandler.bind(null, props.userEmail);

  return (
    <LinkElement id="mailto__button" link={link} className={`link link--action-button ${props.icon}`} title={props.title}
                 onClick={click} />
  );
};

/**
 * Handle email click. Here we track GA.
 *
 * @param {string} [userEmail] userEmail
 */
function clickHandler(userEmail) {
  gtm.event(gtm.ACTION_BUTTON_CLICK, {name: 'email', email: userEmail});
}

function replaceVariables(string, props) {
  return string.
    replace('{link}', isBrowser() ? window.location.href : '').
    replace('{dynamicTitle}', props.dynamicTitle).
    replace('{auctionTitle}', props.auctionTitle).
    replace('{auctionDate}', props.auctionDate ? auctionDateToString(props.auctionDate, props.locale) : '');
}

EmailButton.propTypes = {
  to: React.PropTypes.string,
  body: React.PropTypes.string,
  subject: React.PropTypes.string,
  title: React.PropTypes.string,
  userEmail: React.PropTypes.string,
  dynamicTitle: React.PropTypes.string,
  auctionTitle: React.PropTypes.string,
  icon: React.PropTypes.string
};

EmailButton.defaultProps = {
  to: '',
  body: '',
  subject: '',
  title: '',
  userEmail: '',
  dynamicTitle: '',
  icon: 'link--email'
};

export default EmailButton;



