import React from 'react';
import './catalog-highlight.scss';
import ImageElement from 'components/images/ImageElement';
import LinkComp from 'components/linkComp';
import _ from 'helpers/lodash';
import {setImageSize, buildLotLink, classes} from 'helpers/utils';
import gtm from 'helpers/gtm';
import {createBlock, createText} from 'helpers/content-type-generators';
import EntryFactory from 'ui/EntryFactory';

const BULLETS_AMOUNT = 6;

/*
 * If we have ['1.jpg', '2.jpg', '3.jpg', '4.jpg']
 * we don't show first and last images (1.jpg & 4.jpg).
 * Rotation Images - images without first & last.
 * If we don't have rotationImages - we don't show component.
 * If rotationImages length is 1, then we don't show < 1/1 > switcher.
 */
export default class CatalogHighlightComp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {imageIndex: 0};
  }

  nextImage(max) {
    this.setState({imageIndex: this.calcIndex(max, ++this.state.imageIndex)});
    gtm.event(gtm.LOTHIGHLIGHT_SLIDESHOW_CLICK, {action: gtm.FORWARD});
  }

  prevImage(max) {
    this.setState({imageIndex: this.calcIndex(max, --this.state.imageIndex)});
    gtm.event(gtm.LOTHIGHLIGHT_SLIDESHOW_CLICK, {action: gtm.BACK});
  }

  /**
   * Returns appropriate index in bound of [0...lastImageIndex]
   * @param {number} max lastImageIndex
   * @param {number} index current index that we need to translate, e.g. 3, 5, -1, 0
   * @returns {number}
   */
  calcIndex(max, index) {
    const limit = max + 1;
    return (limit + index) % limit;
  }

  render() {
    const {lots, extras, seeMoreText, lotHighlightImgWidth, lotHighlightImgHeight, isShopEvent} = this.props;
    const highlightedLot = lots.reduce((result, lot) => {
      const newResult = lot.isMoneyItem ? lot : result;
      return result || newResult;
    }, null);
    const lotIndex = lots.indexOf(highlightedLot);

    if (!highlightedLot) return null;
    if (_.get(highlightedLot.soldPrice, 'value')) return null;

    const images = highlightedLot.images;

    if (images.length < 3) return null; // Expected at least 3 images, as first and last is ignored.

    let rotationImages = images.slice(1, -1).reduce((result, object) => {
      result.push(object.url);
      return result;
    }, []);

    const lastImageIndex = rotationImages.length - 1;
    const spaLink = highlightedLot && buildLotLink(highlightedLot, extras);
    const classname = classes({
      'catalog-component__highlighted-lot clearfix': true,
      ['lot-index__' + lotIndex]: true,
      'no-switch-images': !lastImageIndex
    });
    const title = isShopEvent ? highlightedLot.title : highlightedLot.catalogNumber + ' | ' + highlightedLot.title;

    return (<div className={classname}>
      <div className="catalog-component__arrow-wrapper">
        <div className="catalog-component__arrow" />
      </div>
      <div className="catalog-component__highlighted-lot-content clearfix">
        <div className="sm-col md-col-6 catalog-component__highlighted-details">
          <ImageElement
            imageUrl={setImageSize(lotHighlightImgHeight, lotHighlightImgWidth, rotationImages[this.state.imageIndex])}
            extras={extras}
            title={highlightedLot.title} />
        </div>
        <div className="sm-col md-col-6 catalog-component__highlighted-details">
          <LinkComp spaLink={spaLink} extras={extras}>
            {title}
          </LinkComp>
          <EntryFactory module={this.buildBulletPointsList(highlightedLot)} extras={this.props.extras} />
          <div className="catalog-component__readmore">
            <LinkComp spaLink={spaLink} extras={extras} className="link link--with-icon"> {seeMoreText}</LinkComp>
          </div>
          <div className="catalog-component__switchers">
            <span className="catalog-component__left-arrow" onClick={this.prevImage.bind(this, lastImageIndex)} />
            {this.state.imageIndex + 1} / {lastImageIndex + 1}
            <span className="catalog-component__right-arrow" onClick={this.nextImage.bind(this, lastImageIndex)} />
          </div>
        </div>
      </div>
    </div>);
  }

  /**
   * Build the specific amount of Bullet Points for highlighted lot
   * @param {Object} highlightedLot Lot which is highlighted
   * @returns {Object} block with rendered bullet points
   */
  buildBulletPointsList = (highlightedLot) => {
    const {extras} = this.props;
    const features = _.get(highlightedLot, 'features', []).slice(0, BULLETS_AMOUNT);

    return createBlock([createText(' - ' + features.join('\n - '), extras)], {
      size: 12,
      className: 'catalog-component__details-bullet-points'
    }, extras);
  }
}

CatalogHighlightComp.propTypes = {
  lots: React.PropTypes.array,
  seeMoreText: React.PropTypes.string,
  extras: React.PropTypes.object,
  lotHighlightImgWidth: React.PropTypes.number,
  lotHighlightImgHeight: React.PropTypes.number,
  isShopEvent: React.PropTypes.bool
};

CatalogHighlightComp.defaultProps = {
  lotHighlightImgWidth: 450,
  lotHighlightImgHeight: 337
};



