import React, { PropTypes } from 'react';
import { connect } from 'react-redux';
import ImageElement from 'components/images/ImageElement';
import ButtonElement from 'components/buttons/ButtonElement';
import CalendarButton from 'components/action-button-calendar/CalendarButton';
import PrintButton from 'components/action-button-print/PrintButton';
import EmailButton from 'components/action-button-email/EmailButton';
import HCalendar from '../hcalendar/HCalendar';
import MarkdownElement from 'components/markdown/MarkdownElement';
import LinkComp from 'components/linkComp';
import moment from 'moment';
import * as UserActions from 'ui/actions/UserActions';
import _ from 'helpers/lodash';
import { bindActionCreators } from 'redux';
import { auctionDateToString, calculateDuration, convertToCity, setImageSize, buildAuctionLink } from '../helpers/utils';
import FlowPlayerElement from 'live-stream/FlowPlayerElement';
import { classes, entityStringProcessor } from 'helpers/utils';
import { getEntityText } from 'helpers/streamUtils';
import './auction-teaser.scss';

const auctionTeaserClasses = (isLive) => classes({
  'auction-teaser section-wrapper clearfix': true,
  'live-stream': isLive,
  'vevent': true
});

const auctionTeaserLeftPartClasses = (isLive) => classes({
  'auction-teaser__left-part px1 sm-col sm-col-12 newgrid': true,
  'md-col-7': isLive,
  'md-col-6': !isLive
});

const auctionTeaserRightPartClasses = (isLive) => classes({
  'auction-teaser__right-part px1 sm-col sm-col-12 newgrid': true,
  'md-col-5': isLive,
  'md-col-6': !isLive
});

export class AuctionTeaserComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      countDownString: null,
      minutesPassed: 0,
      auctionDate: ''
    };
  }

  componentWillReceiveProps(nextProps) {
    const {locale} = nextProps.module;
    const wasLoggedIn = _.get(this.props, 'userLoggedIn');
    const isLoggedIn = _.get(nextProps, 'userLoggedIn');
    const auctionNumber = _.get(this.props, 'data.auction.auctionNumber');
    const nextAuctionNumber = _.get(nextProps, 'data.auction.auctionNumber');

    if ((isLoggedIn && !wasLoggedIn)
        || (auctionNumber !== nextAuctionNumber)) {
      this.props.actions.getAuctionData(nextAuctionNumber);
    }
    if (locale !== this.props.module.locale) {
      const date = _.get(this.props.data, 'auction.startDate', '');
      this.setState({
        auctionDate: auctionDateToString(date, locale)
      });
      this.startCountdown(locale);
    }
  }

  shouldComponentUpdate(nextProps) {
    const nextAuctionNumber = _.get(nextProps, 'data.auction.auctionNumber', -1);
    return nextAuctionNumber > 0;
  }

  componentWillMount() {
    const { module: {locale}, data, userLoggedIn } = this.props;
    const auctionNumber = _.get(this.props, 'data.auction.auctionNumber');
    const date = _.get(data, 'auction.startDate', '');
    if (userLoggedIn && auctionNumber) {
      this.props.actions.getAuctionData(auctionNumber);
    }

    this.setState({
      auctionDate: auctionDateToString(date, locale)
    });
  }

  componentDidMount() {
    const {data} = this.props;

    this.startCountdown();
    this.countAuctionTime();
    this.toggleAnnouncements(_.get(data, 'auction.businessEntity', '') === 'NYC' &&
      !_.get(data, 'auction.auctionAnnouncementText', ''));
  }

  componentWillUnmount() {
    clearInterval(this.countDownInterval);
    this.toggleAnnouncements(false);
  }

  renderBackLink() {
    const {extras, backToCalendarLink, backToCalendarText} = this.props;

    let linkProps = {
      spaLink: extras.addLocaleToUrl(backToCalendarLink),
      extras: extras,
      className: 'link link--with-icon link--back'
    };

    return (
      <div className="lot-teaser__back-to-auction">
        <LinkComp {...linkProps}>
          {backToCalendarText}
        </LinkComp>
      </div>
    );
  }

  render() {
    const {
      buttonText,
      buttonDescription,
      extras,
      data,
      topTitle,
      bottomLinks,
      liveText,
      youAreRegistered,
      user,
      userLoggedIn
    } = this.props;
    const auction = data.auction || {};
    const catalogUrl = auction.catalogUrl;
    const { countDownString, minutesPassed, auctionDate } = this.state;
    const auctionStartDate = _.get(this.props.data, 'auction.startDate', '');
    const unknown = _.get(user, 'unknown');
    const isLive = auction.isLive || auction.isOpen;
    const businessEntity = convertToCity(auction.businessEntity);
    const { externalNumber, auctionNumber, startDate, title } = auction;
    const isRegisteredUserForAuction = _.get(user, ['auctions', auctionNumber, 'UserIsRegistered']);
    const link = buildAuctionLink(auction, extras);
    const registrationLink = `${link}/registration`;
    let entityText = this.getEntityText(auction.isUkTeam, topTitle, externalNumber, businessEntity);
    let bottomLinksNode = '';
    let buttonDescriptionElement = (<div className="auction-teaser__button-description">
        {buttonDescription}
      </div>);
    var countDown, heading3, registration;

    entityText = entityStringProcessor(entityText, [_.get(data, 'auction.auctionType', '')]);

    if (bottomLinks) {
      bottomLinksNode = (<div className="auction-teaser__bottom-links">
        <MarkdownElement text={bottomLinks} extras={extras} />
      </div>);
    }

    if (countDownString) {
      countDown = (<span className="auction-teaser__countdown">{countDownString}</span>);
    }

    // if auchentication checked
    if (!unknown) {
      registration = [<ButtonElement key="btn" text={buttonText} buttonType="primary" url={registrationLink} extras={extras} />];
      //hide button if not live
      if (!isLive && isRegisteredUserForAuction === true && userLoggedIn) {
        registration = [];
      }
      // if user registered for an auction
      if (isRegisteredUserForAuction === true && userLoggedIn) {
        buttonDescriptionElement = null;
        registration.push(<div className="auction-teaser__already-registered" key="reg">{youAreRegistered}</div>);
      }
    }

    heading3 = liveText ?
      <span className="auction-teaser__live-text">{liveText.replace('{auctionMinutes}', minutesPassed)}</span> :
      <div>
        <HCalendar dtStart={startDate} url={link} description={auction.description} location={auction.businessEntity} />
        <span className="auction-teaser__date">{auctionDate}</span>
        {countDown}
      </div>;

    return (<div className={auctionTeaserClasses(isLive)}>
      <div className="col-12 clearfix">
        <div className="px1 clearfix">
          <div className="left">
            {this.renderBackLink()}
          </div>
        </div>
      </div>

      <div className={auctionTeaserLeftPartClasses(isLive)}>
        <div className="auction-teaser__img">
          {this.getAuctionTeaser()}
        </div>
        {this.getAuctionTeaserText(isLive)}
      </div>
      <div className={auctionTeaserRightPartClasses(isLive)}>
        <div className="auction-teaser__entity">{entityText}</div>
        <h1 className="auction-teaser__title summary">{title}</h1>
        {heading3}
        {buttonDescriptionElement}
        {registration}

        <div className="auction-teaser__actions-button">
          <CalendarButton extras={extras} auction={auction} title={this.props.calendarButtonTitle} />
          {catalogUrl && <PrintButton title={this.props.downloadCatalogButtonTitle} onClick={()=>{}} link={`/${catalogUrl}`} target="_blank" />}
          <EmailButton
            body={this.props.emailBody}
            subject={this.props.emailSubject}
            title={this.props.emailButtonTitle}
            auctionTitle={auction.title}
            auctionURL={extras.location.pathname}
            auctionDate={auctionStartDate}
            userEmail={user.email}
            locale={extras.locale}
          />
        </div>
        {bottomLinksNode}
      </div>
    </div>);
  }

  toggleAnnouncements = (isAnnouncementHidden) => {
    if (isAnnouncementHidden) {
      document.body.classList.add('no-announcements');
    }
    else {
      document.body.classList.remove('no-announcements');
    }
  };

  getEntityText = getEntityText

  getAuctionTeaserText(isLive) {
    if (isLive) return;

    const { data } = this.props;
    const imageTitle = _.get(data, 'auction.imageTitle', '');

    return (<div className="auction-teaser__img-text">{imageTitle}</div>);
  }

  getAuctionTeaser() {
    const {
      extras,
      data,
      imgWidth,
      imgHeight,
      config,
      liveStream
    } = this.props;

    const isLive = _.get(data, 'auction.isLive') || _.get(data, 'auction.isOpen');

    if (isLive) {
      const unavailableStreamText = _.get(liveStream, 'content.unavailableStreamText.content.text', '');
      const unavailableStreamAction = _.get(liveStream, 'content.unavailableStreamAction.content', {});

      _.set(unavailableStreamAction, 'url', this.registrationLink);

      return (<FlowPlayerElement
            extras={extras}
            streamUrl={_.get(liveStream, 'content.streamUrl')}
            config={config}
            currentAuction={data.auction}
            flowPlayerLicense={_.get(liveStream, 'content.flowPlayerLicense')}
            unavailableStreamText={unavailableStreamText}
            unavailableStreamAction={unavailableStreamAction}
          />);
    }

    let imageUrl = _.get(data, 'auction.image', '');
    let imageTitle = _.get(data, 'auction.imageTitle', '');

    return (<ImageElement
            imageUrl={setImageSize(imgHeight, imgWidth, imageUrl)}
            extras={{...extras, lazyLoading: false }}
            title={imageTitle} />);
  }

  startCountdown(newLocale) {
    const { interval, data, module: {locale} } = this.props;
    const currentLocate = newLocale || locale;
    let eventDate = _.get(data, 'auction.startDate', '');
    let diffTime = calculateDuration(eventDate, currentLocate);
    let daysLeft = diffTime.asDays();

    if (daysLeft >= 2 || daysLeft < 0) {
      return;
    }

    this.setState({
      countDownString: this.getCountDownString(diffTime)
    });

    clearInterval(this.countDownInterval);

    this.countDownInterval = setInterval(() => {
      diffTime = calculateDuration(eventDate, currentLocate, diffTime.asMilliseconds() - interval);
      this.setState({ countDownString: this.getCountDownString(diffTime)});
    }, interval);
  }

  getCountDownString(duration) {
    if (duration.milliseconds() < 0) {
      return '0 : 00 : 00';
    }
    let minutesAndSeconds = moment(duration._data).format(' : mm : ss');
    return parseInt(duration.asHours()) + minutesAndSeconds;
  }

  countAuctionTime() {
    const { data, module: {locale} } = this.props;
    let eventDate = _.get(data, 'auction.startDate', '');
    let diffTime = calculateDuration(eventDate, locale);

    this.setState({
      minutesPassed: Math.abs(parseInt(diffTime.asMinutes()))
    });
  }
}

AuctionTeaserComponent.propTypes = {
  bottomLinks: PropTypes.string,
  buttonText: PropTypes.string.isRequired,
  topTitle: PropTypes.string,
  buttonDescription: PropTypes.string,
  interval: PropTypes.number,
  liveText: PropTypes.string,
  extras: PropTypes.any,
  data: PropTypes.object,
  module: PropTypes.object,
  actions: PropTypes.object,
  liveStream: PropTypes.object,
  user: PropTypes.object,
  imgWidth: PropTypes.number,
  imgHeight: PropTypes.number,
  youAreRegistered: PropTypes.string,
  userLoggedIn: PropTypes.bool,
  config: PropTypes.object,
  auctionNumberTemplateId: PropTypes.string,
  businessEntityTemplateId: PropTypes.string,
  downloadCatalogButtonTitle: React.PropTypes.string,
  backToCalendarLink: React.PropTypes.string,
  backToCalendarText: React.PropTypes.string,
  calendarButtonTitle: React.PropTypes.string,
  emailButtonTitle: React.PropTypes.string,
  emailBody: React.PropTypes.string,
  emailSubject: React.PropTypes.string
};

AuctionTeaserComponent.defaultProps = {
  bottomLinks: '',
  buttonText: '',
  topTitle: '',
  buttonDescription: '',
  interval: 1000,
  imgWidth: 460,
  imgHeight: 346,
  youAreRegistered: 'You are registered!',
  liveStream: {},
  config: {},
  auctionNumberTemplateId: '{auctionNumber}',
  businessEntityTemplateId: '{bussinesEntity}',
  emailBody: '',
  emailSubject: ''
};

function mapStateToProps(state) {
  return {
    module: state.modules.module,
    data: state.modules.data,
    userLoggedIn: !!state.user.email,
    user: state.user,
    config: state.config
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(UserActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AuctionTeaserComponent);



