import React from 'react';
import './price.scss';

const currencyMapping = {
  USD: '$',
  EUR: '€'
};

export default class PriceComp extends React.Component {
  render() {
    const {
      value,
      currency,
      decimalPlaces,
      extras = {locale:'de'}} = this.props;
    const currencySign = currencyMapping[currency];
    const currencyTag = (<span key="currency" className="currency" dangerouslySetInnerHTML={{__html: currencySign}} />);

    let price = [numberFormatting(value, extras.locale, decimalPlaces)];

    if (!!~['en-US', 'en'].indexOf(extras.locale)) {
      price.unshift(currencyTag);
    }
    else {
      price.push(currencyTag);
    }

    return (<span className="price">{price}</span>);
  }
}
PriceComp.propTypes = {
  extras: React.PropTypes.object,
  value: React.PropTypes.number,
  currency: React.PropTypes.string,
  decimalPlaces: React.PropTypes.number
};

PriceComp.defaultProps = {
  decimalPlaces: 0,
  currency: 'EUR'
};
/*
Numbers   Euro    Dollars with decimals
INT #,### € #,### $ #,### #,###.00
US  #,### € #,### $ #,### #,###.00
DE  #.### #.### € #.### $ #.###,00
*/
export function renderAsString(value, currency, locale, decimalPlaces) {
  const currencySign = currencyMapping[currency];
  let price = [format(value, locale, decimalPlaces)];

  if (!!~['en-US', 'en'].indexOf(locale)) {
    price.unshift(currencySign);
  }
  else {
    price.push(currencySign);
  }

  return price.join(' ');
}

export function renderCurrency(currency) {
  return currencyMapping[currency];
}

function numberFormatting(number, locale, decimalPlaces) {
  return <span key="number">{format(number, locale, decimalPlaces)}</span>;
}

function format(n, locale = 'en', decimalPlaces = 0) {
  const fractionalCapacity = Math.pow(10, decimalPlaces);
  const rounded = Math.round(n * fractionalCapacity) / fractionalCapacity;

  const roundedAsString = String(parseFloat(rounded).toFixed(decimalPlaces));
  const currenctSeparator = roundedAsString.indexOf('.') !== -1 ? '.' : ',';
  const [integer, fractional] = String(roundedAsString).split(currenctSeparator);

  let result = formatThousands(integer, locale);

  if (fractional) {
    const separator = ['en-US', 'en'].indexOf(locale) !== -1 ? '.' : ',';
    result = result.concat(separator, fractional);
  }
  return result;
}

function formatThousands(value, locale) {
  let sep = '$1.';
  if (['en-US', 'en'].indexOf(locale) !== -1) {
    sep = '$1,';
  }
  return String(Math.floor(value)).replace(/(\d)(?=(\d{3})+$)/g, sep);
}



