import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import ButtonElement from 'components/buttons/ButtonElement';
import ImageElement from 'components/images/ImageElement';
import HCalendar from 'hcalendar/HCalendar';
import ActionButtons from './action-buttons/ActionButtons';
import LinkComp from 'components/linkComp';
import * as UserActions from 'ui/actions/UserActions';
import _ from 'helpers/lodash';
import constants from 'const';
import {bindActionCreators} from 'redux';
import {
  auctionDateToString,
  buildAuctionLink
} from '../helpers/utils';
import FlowPlayerElement from 'live-stream/FlowPlayerElement';
import './shop-event-teaser.scss';

export class AuctionTeaserComponent extends React.Component {
  shouldComponentUpdate(nextProps) {
    const nextAuctionNumber = _.get(nextProps, 'data.auction.auctionNumber', -1);
    return nextAuctionNumber > 0;
  }

  componentWillMount() {
    const auctionNumber = _.get(this.props, 'data.auction.auctionNumber');
    this.props.actions.getAuctionData(auctionNumber); // ?
  }

  renderBackLink() {
    const {extras, backToShopHubLink, backToShopHubText} = this.props;

    let linkProps = {
      spaLink: extras.addLocaleToUrl(backToShopHubLink),
      extras: extras,
      className: 'link link--with-icon link--back'
    };

    return (
      <div className="lot-teaser__back-to-auction">
        <LinkComp {...linkProps}>
          {backToShopHubText}
        </LinkComp>
      </div>
    );
  }

  render() {
    const {
      extras,
      data,
      topTitle
    } = this.props;
    const isLive = this.isLive();
    const auction = data.auction || {};
    const date = _.get(data, 'auction.startDate', '');
    const locale = this.props.module.locale;
    const {startDate, title} = auction;
    const link = buildAuctionLink(auction, extras);

    const announceBlock = (
      <div>
        <HCalendar dtStart={startDate} url={link} description={auction.description} location={auction.businessEntity} />
        <div className="shop-event-teaser__date">
          {this.props.eventOpens} {auctionDateToString(date, locale)}
        </div>

        <div>
          <ButtonElement text={this.props.buttonDescription} buttonType="secondary" />
        </div>
      </div>
    );

    const liveBlock = (
      <div className="shop-event-teaser__actions-button">
        <ActionButtons {...this.props} />
      </div>
    );

    const showVideo = () => {
      if (auction.auctionVideo) {
        return (
          <FlowPlayerElement
            extras={extras}
            isVideoElement
            videoUrl={auction.auctionVideo}
            splash={auction.image}
          />
        );
      }

      return <ImageElement imageUrl={auction.image} alt={auction.imageTitle} title={auction.imageTitle} />;
    };

    return (
      <div className="section-wrapper">
        <div className="shop-event-teaser__back">
          <div className="px1 left">
            {this.renderBackLink()}
          </div>
        </div>
        <div>
          <div className="shop-event-teaser clearfix vevent">
            <div className="shop-event-teaser__left-part px1 sm-col sm-col-12 md-col-8">
              <div className="shop-event-teaser__img">
                {showVideo()}
              </div>
            </div>
            <div className="shop-event-teaser__right-part px1 sm-col sm-col-12 md-col-4">
              <div className="shop-event-teaser__entity">{topTitle}</div>
              <h1 className="shop-event-teaser__title summary">{title}</h1>
              <p className="shop-event-teaser__description">{this.props.data.auction.teaserText}</p>

              {isLive ? liveBlock : announceBlock}
            </div>
          </div>
        </div>
      </div>
    );
  }

  isLive = () => {
    return constants.AUCTION_STATES[this.props.data.auction.workflowStage] === 'live';
  }
}

AuctionTeaserComponent.propTypes = {
  buttonText: PropTypes.string.isRequired,
  topTitle: PropTypes.string,
  buttonDescription: PropTypes.string,
  eventOpens: PropTypes.string,
  extras: PropTypes.any,
  data: PropTypes.object,
  module: PropTypes.object,
  actions: PropTypes.object,
  recommendEmailButtonTitle: React.PropTypes.string,
  recommendEmailBody: React.PropTypes.string,
  recommendEmailSubject: React.PropTypes.string,
  questionEmailButtonTitle: React.PropTypes.string,
  backToShopHubLink: React.PropTypes.string,
  backToShopHubText: React.PropTypes.string,
  questionEmailBody: React.PropTypes.string,
  questionEmailRecipient: React.PropTypes.string,
  questionEmailSubject: React.PropTypes.string
};

function mapStateToProps(state) {
  return {
    module: state.modules.module,
    data: state.modules.data,
    userLoggedIn: !!state.user.email,
    user: state.user,
    config: state.config,
    dynamicTitle: state.modules.module.content.dynamicTitle
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(UserActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AuctionTeaserComponent);



