import React from 'react';
import LinkElement from '../link-element/LinkElement';

const CalendarButton = (props) => {
  const link = generateURL(props.auction, props.extras.locale);

  return (
    <LinkElement className="link link--action-button link--calendar" link={link} title={props.title} download="" />
  );
};

function generateURL(auction, locale) {
  const {auctionNumber, slug} = auction;

  return `/_/calendar/${locale}/${auctionNumber}/${slug}`;
}

CalendarButton.propTypes = {
  auction: React.PropTypes.object,
  extras: React.PropTypes.object,
  title: React.PropTypes.string
};

CalendarButton.defaultProps = {
  auction: {},
  extras: {},
  title: ''
};

export default CalendarButton;



