import React, {PropTypes} from 'react';
import {connect} from 'react-redux';

import LinkComp from 'components/linkComp';
import ImageElement from 'components/images/ImageElement';
import PriceComp from 'components/priceComp';
import PriceTooltip from 'components/price-tooltip/PriceTooltip';
import VGBildPlaceholder from 'components/vgbild-placeholder/VGBildPlaceholder';
import {setImageSize, buildLotLink, dateToString, convertToCity} from 'helpers/utils';
import _ from 'helpers/lodash';

import './styles.scss';


export class ALPResultGrid extends React.Component {

  render() {
    const auction = this.props.auction;

    const list = this.renderList();
    const auctionFinished = this.props.auctionIsFinished.replace('{auctionNumber}', auction.externalNumber);

    const date = dateToString(auction.startDate, this.props.extras.locale, false, true);
    const location = convertToCity(auction.businessEntity);

    return (
      <div className="alp-result-grid section-wrapper">
        <div className="alp-result-grid-header px1">
          <h3>{auctionFinished}</h3>
          <h1>{this.props.auction.title}</h1>
          <span>{date}, {location}</span>
        </div>

        <ul className="">
          {list}
        </ul>
      </div>
    );
  }

  renderList() {
    return this.props.auction.lots.map(this.renderLot);
  }

  renderLot = (lot, index) => {
    if (!_.get(lot, 'status.isPublic')) return;

    const extras = this.props.extras;
    const url = buildLotLink(lot, extras);
    const title = lot.title;
    const priceDetails = this.renderPriceDetails(lot);

    const imgURL = _.get(lot, 'images[0].url');
    const lotImage = extras.isMobile
      ? setImageSize(this.props.mobileLotImgHeight, this.props.mobileLotImgWidth, imgURL)
      : setImageSize(this.props.lotImgHeight, this.props.lotImgWidth, imgURL);

    const protectedSoldLot = lot.status.saleStatus === 'Sold' && lot.representedVgBild;
    const image = protectedSoldLot ?
      <VGBildPlaceholder className="alp" /> : <ImageElement imageUrl={lotImage} title={title} extras={extras} />;

    return (
      <li className="alp-result-grid-item" key={index}>
        <LinkComp className="" spaLink={url} extras={extras}>
          {image}
          <span className="alp-result-grid-item-title-wrapper">
            <span className="alp-result-grid-item-title-number">{lot.catalogNumber} | </span>
            <span className="alp-result-grid-item-title">{title}</span>
          </span>
        </LinkComp>
        <div className="alp-result-grid-price-details">
          {priceDetails}
        </div>
      </li>
    );
  };

  renderPriceDetails(lot) {
    const extras = this.props.extras;

    let saleStatus = lot.status.saleStatus;
    if (!saleStatus) return null;
    saleStatus = saleStatus.toLowerCase();

    if (saleStatus === 'sold') {
      let priceObj = lot.soldPrice;
      let priceDetails = this.props.priceDetailsInfoSold;

      return (
        <div className={saleStatus}>
          <span className="alp-result-grid-price-details-label">{this.props.statusSold}</span>
          <PriceComp value={priceObj.value} currency={priceObj.currency} extras={extras} />
          <PriceTooltip {...this.props} data={{...this.props}} lot={lot} priceDetailsInfo={priceDetails} />
        </div>
      );
    }

    if (saleStatus === 'notsold') {
      let priceDetails = this.props.priceDetailsInfoNotSold;

      return (
        <div className={saleStatus}>
          <span className="alp-result-grid-price-details-label">{this.props.statusNotSold}</span>
          <PriceTooltip {...this.props} data={{...this.props}} lot={lot} priceDetailsInfo={priceDetails} />
        </div>
      );
    }

    if (saleStatus === 'soldconditionally') {
      let priceObj = lot.soldPrice;
      let priceDetails = this.props.priceDetailsInfoNotSold;

      return (
        <div className={saleStatus}>
          <span className="alp-result-grid-price-details-label">{this.props.statusConditionalSale}</span>
          <PriceComp value={priceObj.value} currency={priceObj.currency} extras={extras} />
          <PriceTooltip {...this.props} data={{...this.props}} lot={lot} priceDetailsInfo={priceDetails} />
        </div>
      );
    }

    if (saleStatus === 'notsoldreservenotmet') {
      return (
        <div className={saleStatus}>
          <span className="alp-result-grid-price-details-label">{this.props.statusReserveNotMet}</span>
        </div>
      );
    }
  }
}

ALPResultGrid.propTypes = {
  auction: PropTypes.object,
  extras: PropTypes.object,
  lotImgWidth: PropTypes.number,
  lotImgHeight: PropTypes.number,
  mobileLotImgWidth: PropTypes.number,
  mobileLotImgHeight: PropTypes.number,
  priceDetailsInfoSold: PropTypes.string,
  priceDetailsInfoNotSold: PropTypes.string,
  statusSold: PropTypes.string,
  statusNotSold: PropTypes.string,
  statusConditionalSale: PropTypes.string,
  statusReserveNotMet: PropTypes.string,
  auctionIsFinished: PropTypes.string
};

ALPResultGrid.defaultProps = {
  auction: {
    lots: []
  },
  lotImgWidth: 300,
  lotImgHeight: 225,
  mobileLotImgWidth: 95,
  mobileLotImgHeight: 71
};

function mapStateToProps(state) {
  return {
    auction: state.modules.data.auction
  };
}

export default connect(mapStateToProps)(ALPResultGrid);



