import React, {PropTypes} from 'react';
import LinkComp from 'components/linkComp';
import PriceComp from 'components/priceComp';
import PriceTooltip from 'components/price-tooltip/PriceTooltip';
import VGBildPlaceholder from 'components/vgbild-placeholder/VGBildPlaceholder';
import {
  setImageSize,
  buildLotLink
} from 'helpers/utils';
import _ from 'helpers/lodash';
import ImageElement from 'components/images/ImageElement';
import ButtonElement from 'components/buttons/ButtonElement';
import {classes} from '../helpers/utils.js';

export default class CatalogItem extends React.Component {
  render() {
    const {lot} = this.props;
    return _.get(lot, 'status.isAvailable') ? this.renderLot(lot) : this.renderInvalidLot();
  }

  renderLot(lot) {
    const {
      title,
      startingBid,
      totalPrice,
      soldPrice,
      catalogNumber,
      lotVideo,
      lotNumber,
      auction
    } = lot;

    const {
      extras,
      startingPrice,
      price,
      sold,
      sale,
      watchVideo,
      enterAbsenteeBidText,
      enableAbsenteeBidding,
      isShopEvent,
    } = this.props;

    const isMobile = extras.isMobile;
    const isLive = _.get(auction, 'isLive') || _.get(auction, 'isOpen');
    const lotLink = buildLotLink(lot, extras);
    const watchVideoLink = lotVideo && (<LinkComp link={lotLink} extras={extras} className="link link--with-icon link--video">{watchVideo}</LinkComp>);

    let absenteeBidButton = null;
    if (!isLive && !isShopEvent && enableAbsenteeBidding) {
      absenteeBidButton = (<ButtonElement
        text={enterAbsenteeBidText}
        buttonType="primary"
        spaUrl={this.getAbsenteeBidURL(lotNumber)}
        extras={extras}
        className="catalog-component__absentee-bid" />);
    }

    let priceLabel = startingPrice;
    let isSold = false;
    let isSale = false;
    let priceObj = startingBid;

    if (isShopEvent) {
      priceLabel = price;
      priceObj = totalPrice;

      if (soldPrice && soldPrice.value) {
        priceLabel = sold;
        priceObj = soldPrice;
        isSold = true;
      }

      if (lot.reducePercentage && !isSold) {
        priceLabel = sale;
        isSale = true;
      }
    }

    const protectedSoldLot = lot.representedVgBild && isSold;

    const catalogItemClasses = classes({
      'catalog-component__lot-price': true,
      'is-sold': isSold,
      'is-sale': isSale,
      'shop-event-item': isShopEvent
    });

    let priceEl = (
      <div className={catalogItemClasses}>
        <small>{priceLabel}
          <PriceComp value={priceObj.value} currency={priceObj.currency} extras={extras} />
          {isSale ? <PriceComp value={lot.originalPrice.value} currency={lot.originalPrice.currency} extras={extras} /> : null}
          <PriceTooltip {...this.props} data={{...this.props}} />
        </small>
      </div>
    );

    //design should be reviewed 
    if (isShopEvent && isMobile && isSale) {
      priceEl = (
        <div className={catalogItemClasses}>
          <small>
            <span>{price}
            <PriceComp value={lot.originalPrice.value} currency={lot.originalPrice.currency} extras={extras} />
              </span>
          </small>
          <small>{sale}
            <PriceComp value={priceObj.value} currency={priceObj.currency} extras={extras} />
            {!isSold && <PriceTooltip {...this.props} data={{...this.props}} />}
          </small>
        </div>
      );
    }

    const linkEl = (
      <LinkComp spaLink={lotLink} extras={extras}>
        {!isShopEvent && <span className="catalog-component__catalog-number">{catalogNumber + ' | '}</span>}
        {title}
      </LinkComp>
    );

    let image = (
      <div className="catalog-component__lot-image">
        {this.createLotImage(lot)}
      </div>
    );

    if (protectedSoldLot) image = <VGBildPlaceholder className="alp" />;

    return (<div className="catalog-component__lot px1 sm-col sm-col-12 md-col-4 clearfix">
      <LinkComp spaLink={lotLink} extras={extras}>
        {image}
      </LinkComp>
      <div className="catalog-component__lot-details">
        {isShopEvent && !isMobile ? priceEl : linkEl}
        {isShopEvent && !isMobile ? linkEl : priceEl}
        {absenteeBidButton}
        {!protectedSoldLot && watchVideoLink}
      </div>
    </div>);
  }

  renderInvalidLot() {
    const {
      invalidLotText,
      lotImgHeight,
      lotImgWidth,
      extras
    } = this.props;
    const imgStyle = {
      width: extras.isMobile ? '' : lotImgWidth,
      height: extras.isMobile ? '' : lotImgHeight
    };

    return (<div className="catalog-component__lot catalog-component__invalid-lot px1 sm-col sm-col-12 md-col-4">
      <div className="catalog-component__lot-image" style={imgStyle}>
        <span className="catalog-component__lot-image__no-img" />
      </div>
      <div className="catalog-component__invalid-msg">
        {invalidLotText}
      </div>
    </div>);
  }

  getAbsenteeBidURL = (lotNumber) => {
    const {
      absenteeBidUrl,
      userLoggedIn,
      absenteeBidUrlForLoggedInUser,
      extras
    } = this.props;

    if (userLoggedIn) {
      return extras.addLocaleToUrl(absenteeBidUrlForLoggedInUser).replace('{lotNumber}', lotNumber);
    }

    return extras.addLocaleToUrl(absenteeBidUrl).replace('{lotNumber}', lotNumber);
  };

  createLotImage = (lot) => {
    const {
      mobileLotImgHeight,
      mobileLotImgWidth,
      lotImgHeight,
      lotImgWidth,
      extras
    } = this.props;
    const imgUrl = _.get(lot, 'images[0].url');

    let lotImage = extras.isMobile
      ? setImageSize(mobileLotImgHeight, mobileLotImgWidth, imgUrl)
      : setImageSize(lotImgHeight, lotImgWidth, imgUrl);
    return <ImageElement imageUrl={lotImage} title={lot.title} extras={extras} />;
  }
}

CatalogItem.propTypes = {
  extras: PropTypes.object,
  lotImgWidth: PropTypes.number,
  lotImgHeight: PropTypes.number,
  mobileLotImgWidth: PropTypes.number,
  startingPrice: PropTypes.string,
  price: PropTypes.string,
  sold: PropTypes.string,
  sale: PropTypes.string,
  mobileLotImgHeight: PropTypes.number,
  businessEntity: PropTypes.string,
  absenteeBidUrlForLoggedInUser: PropTypes.string,
  locale: PropTypes.string,
  auction: PropTypes.object,
  lot: PropTypes.object,
  artistsLoyaltyTooltip: React.PropTypes.string,
  ageRestrictionTooltip: PropTypes.string,
  enableAbsenteeBidding: PropTypes.bool,
  userLoggedIn: PropTypes.bool,
  enterAbsenteeBidText: PropTypes.string,
  watchVideo: PropTypes.string,
  priceDetailsInfo: PropTypes.string,
  absenteeBidUrl: PropTypes.string,
  invalidLotText: PropTypes.string,
  lotIndex: PropTypes.number,
  isShopEvent: PropTypes.bool
};



