import React from 'react';
import LinkElement from 'components/link-element/LinkElement';

const PrintButton = (props) => {
  const resultProps = {
    ...props,
    onClick: props.onClick || printPage
  };

  return (
    <LinkElement id="print__button" className="link link--action-button link--print" {...resultProps} />
  );
};

/**
 * Call internal print function.
 */
function printPage() {
  window.print();
}

PrintButton.propTypes = {
  title: React.PropTypes.string,
  onClick: React.PropTypes.func
};

PrintButton.defaultProps = {
  title: ''
};

export default PrintButton;



