import React from 'react';
import RCTooltip from 'rc-tooltip';
import {isHandheld} from 'helpers/device-detect';
import './tooltip.scss';

const Tooltip = (props) => {
  const trigger = isHandheld() ? ['click'] : ['click', 'hover'];

  return (
    <RCTooltip trigger={trigger} {...props}>
      {props.children}
    </RCTooltip>
  );
};

Tooltip.propTypes = {
  children: React.PropTypes.object
};

Tooltip.defaultProps = {};

export default Tooltip;



