import React from 'react';
import Tooltip from 'components/tooltip/Tooltip';
import {businessEntityStringProcessor} from 'helpers/utils';
import _ from 'helpers/lodash';
import './price-tooltip.scss';

const PriceTooltip = (props) => {
  const lot = props.lot;
  const ageRestrictionInfo = lot.ageRestriction ? props.ageRestrictionTooltip : '';
  const key = _.get(lot, 'status.isShop', false) ? 'lot' : 'auction';
  const businessEntity = _.get(props[key], 'businessEntity', 'BER');

  const additionalTooltipItems = (
    <span>
      {ageRestrictionInfo ? (<span><br /> {ageRestrictionInfo}</span>) : null}
      {lot.resaleRights ? (<span><br /> {props.artistsLoyaltyTooltip}</span>) : null}
    </span>
  );

  const tooltipText = <div style={{width: 200}}>{businessEntityStringProcessor(props.priceDetailsInfo,
    businessEntity)}{additionalTooltipItems}</div>;

  return (
    <span className="price-tooltip">
      <Tooltip placement="bottom" overlay={tooltipText}>
        <span className="auctionata-info__icon" />
      </Tooltip>
    </span>
  );
};

PriceTooltip.propTypes = {
  lot: React.PropTypes.object,
  auction: React.PropTypes.object,
  extras: React.PropTypes.object,
  ageRestrictionTooltip: React.PropTypes.string,
  artistsLoyaltyTooltip: React.PropTypes.string,
  priceDetailsInfo: React.PropTypes.string
};

PriceTooltip.defaultProps = {
  priceDetailsInfo: ''
};

export default PriceTooltip;



