import React from 'react';
import Modal from 'react-modal';
import './bidding-forms.scss';
import _ from 'helpers/lodash';
import { connect } from 'react-redux';
import { event } from 'helpers/analitics';
import gtm from 'helpers/gtm';

const languages = ['En', 'De', 'Ru', 'Ch'];

export class BiddingFormsComponent extends React.Component {
  constructor(props) {
    super(props);
    const { businessEntity = 'BER' } = this.props;
    const options = languages.map((lang)=> {
      let businessEntityLowerCase = businessEntity.toLowerCase();
      return {
        label: this.props[`lang${lang}`],
        value: getPdfLink(this.props[`${businessEntityLowerCase}${lang}`],
                              this.props[`${businessEntityLowerCase}${lang}Pdf`])
      };
    }).reduce(removeOptionsWithoutPdfs, []);
    this.state = {
      linkToDownload: options[0] ? options[0].value : '',
      language: languages[0].toLowerCase(),
      options: options,
      modalIsOpened: false
    };
  }
  openModal = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (this.state.options.length == 1) {
      return this.download();
    }
    this.setState({modalIsOpened: true});
  }
  closeModal = () => {
    this.setState({modalIsOpened: false, linkToDownload: this.state.options[0].value});
  }
  selectOption = (e) => {
    this.setState({linkToDownload: this.state.options[e.target.value].value, language:  languages[e.target.value].toLowerCase()});
  }
  download = () => {
    const { externalNumber, dynamicDataConfig } = this.props;
    const { language, linkToDownload } = this.state;
    const action = `${externalNumber || 'information_hub'}_${language}`;
    const eventName = dynamicDataConfig.gtmEvent|| dynamicDataConfig.gaCategory;
    event(dynamicDataConfig.gaCategory, action);
    window.open(linkToDownload);
    this.closeModal();
    gtm.event(eventName, {'form_language': language});
  }
  render() {
    const {
            externalNumber,
            title,
            auctionNameText,
            linkText,
            modalTitle,
            modalBody,
            downloadButton,
            cancelButton
          } = this.props;

    let auctionName;
    if (auctionNameText && externalNumber && title) {
      auctionName = (<h4>{auctionNameText.replace('{auctionNumber}', externalNumber)
                                       .replace('{title}', title)}</h4>);
    }

    return <span className="bidding-forms">
      <a href="#" onClick={this.openModal} className="link link--with-icon link--download link--compact">{linkText}</a>
      <Modal
          isOpen={this.state.modalIsOpened}
          onRequestClose={this.closeModal}
          className="Modal__Bootstrap modal__dialog modal-bidding"
      >
        <div className="modal__header">
          <button type="button" className="modal__header__close"
                  onClick={this.closeModal}>×</button>
          <h3 className="modal__title">{modalTitle}</h3>
        </div>
        <p>
          {modalBody}
        </p>
        {auctionName}
        <div className="select-wrapper">
          <select onChange={this.selectOption}>
            {this.state.options.map(function (opt, idx) {
              return <option value={idx} key={idx}>{opt.label}</option>;
            })}
          </select>
        </div>
        <div className="button-group">
          <a className="button-element primary-button"
            onClick={this.download}>{downloadButton}</a>
          <a className="button-element secondary-button"
            onClick={this.closeModal}>{cancelButton}</a>

        </div>
      </Modal>
    </span>;
  }
}

function getPdfLink(link, pdf) {
  return pdf ? _.get(pdf, 'content.file.url') : link;
}

function removeOptionsWithoutPdfs(opts, el) {
  if (el.value) {
    opts.push(el);
  }
  return opts;
}

BiddingFormsComponent.propTypes = {
  berEn: React.PropTypes.string,
  berRu: React.PropTypes.string,
  berCh: React.PropTypes.string,
  berDe: React.PropTypes.string,
  nycEn: React.PropTypes.string,
  nycRu: React.PropTypes.string,
  nycCh: React.PropTypes.string,
  nycDe: React.PropTypes.string,

  berEnPdf: React.PropTypes.object,
  berRuPdf: React.PropTypes.object,
  berChPdf: React.PropTypes.object,
  berDePdf: React.PropTypes.object,
  nycEnPdf: React.PropTypes.object,
  nycRuPdf: React.PropTypes.object,
  nycChPdf: React.PropTypes.object,
  nycDePdf: React.PropTypes.object,

  langEn: React.PropTypes.string,
  langRu: React.PropTypes.string,
  langCh: React.PropTypes.string,
  langDe: React.PropTypes.string,
  businessEntity: React.PropTypes.string,
  externalNumber: React.PropTypes.number,
  title: React.PropTypes.string,
  auctionNameText: React.PropTypes.string,
  linkText: React.PropTypes.string,
  modalTitle: React.PropTypes.string,
  modalBody: React.PropTypes.string,
  downloadButton: React.PropTypes.string,
  cancelButton: React.PropTypes.string,

  dynamicDataConfig: React.PropTypes.object
};

BiddingFormsComponent.defaultProps = {
  auctionNameText: 'Auction No. {auctionNumber} - {title}',
  linkText: 'Download Form',
  modalTitle: 'Modal Title',
  modalBody: '[Modal Body]',
  downloadButton: 'Download',
  cancelButton: 'Cancel'
};

function mapStateToProps(state, props) {
  return {
    externalNumber: _.get(state, 'modules.data.auction.externalNumber'),
    businessEntity: _.get(state, 'modules.data.auction.businessEntity', props.businessEntity),
    title: _.get(state, 'modules.data.auction.title')
  };
}

export default connect(mapStateToProps)(BiddingFormsComponent);



